<template>
	<div class="">
      <div class="img-cont">
        <i><img src="../assets/img/cert-s.png" alt=""></i>
      </div>

    </div>
</template>

<script>
export default {
  name: 'LookCert',
  data () {
      return {

      }
  },
  created:function(){

    this.getCerList();
  },
  methods: {
    getCerList(){
      let self = this;
      self.$axios.get(self.baseUrl+'/api/v1/admin/certs').then(res =>{
        if(res.data.success){
          //self.cerList = res.data.data;
        }else{
          self.$toast.center('数据获取失败');
        }

      })


    },


   },
   computed: {

   }




}
</script>

<style scoped>
.img-cont{
  width:100%;
  height:100%;
  position: absolute;
  background-color:rgba(0,0,0,0.8);
}
.img-cont i{
  display: inline-block;
  margin:50px auto;
    width:600px;
    /*height:86px;*/
    vertical-align: middle;
}
img{
    width:100%;
    height:100%;
}
</style>



